import { Live2DCubismFramework as cubismmatrix44 } from '../math/cubismmatrix44';
import { Live2DCubismFramework as cubismmodel } from '../model/cubismmodel';
import CubismModel = cubismmodel.CubismModel;
import CubismMatrix44 = cubismmatrix44.CubismMatrix44;

export namespace Live2DCubismFramework {
  /**
   * 
   *
   * 
   */
  export abstract class CubismRenderer {
    /**
     * 
     *
     * @return 
     */
    public static create(): CubismRenderer {
      return null;
    }

    /**
     * 
     */
    public static delete(renderer: CubismRenderer): void {
      renderer = null;
    }

    /**
     * 
     * 
     * @param model 
     */
    public initialize(model: CubismModel): void {
      this._model = model;
    }

    /**
     * 
     */
    public drawModel(): void {
      if (this.getModel() == null) return;

      this.doDrawModel();
    }

    /**
     * Model-View-Projection 
     * 
     * @param matrix44 Model-View-Projection 
     */
    public setMvpMatrix(matrix44: CubismMatrix44): void {
      this._mvpMatrix4x4.setMatrix(matrix44.getArray());
    }

    /**
     * Model-View-Projection 
     * @return Model-View-Projection 
     */
    public getMvpMatrix(): CubismMatrix44 {
      return this._mvpMatrix4x4;
    }

    /**
     * 
     * 0.0~1.01.0
     * @param red 
     * @param green 
     * @param blue 
     * @param alpha 
     */
    public setModelColor(
      red: number,
      green: number,
      blue: number,
      alpha: number
    ): void {
      if (red < 0.0) {
        red = 0.0;
      } else if (red > 1.0) {
        red = 1.0;
      }

      if (green < 0.0) {
        green = 0.0;
      } else if (green > 1.0) {
        green = 1.0;
      }

      if (blue < 0.0) {
        blue = 0.0;
      } else if (blue > 1.0) {
        blue = 1.0;
      }

      if (alpha < 0.0) {
        alpha = 0.0;
      } else if (alpha > 1.0) {
        alpha = 1.0;
      }

      this._modelColor.R = red;
      this._modelColor.G = green;
      this._modelColor.B = blue;
      this._modelColor.A = alpha;
    }

    /**
     * 
     * 0.0~1.0(1.0)
     *
     * @return RGBA
     */
    public getModelColor(): CubismTextureColor {
      return JSON.parse(JSON.stringify(this._modelColor));
    }

    /**
     * 
     * truefalse
     */
    public setIsPremultipliedAlpha(enable: boolean): void {
      this._isPremultipliedAlpha = enable;
    }

    /**
     * 
     * @return true 
     * @return false 
     */
    public isPremultipliedAlpha(): boolean {
      return this._isPremultipliedAlpha;
    }

    /**
     * 
     * truefalse
     */
    public setIsCulling(culling: boolean): void {
      this._isCulling = culling;
    }

    /**
     * 
     * @return true 
     * @return false 
     */
    public isCulling(): boolean {
      return this._isCulling;
    }

    /**
     * 
     * 
     * @param n 
     */
    public setAnisotropy(n: number): void {
      this._anisortopy = n;
    }

    /**
     * 
     * @return 
     */
    public getAnisotropy(): number {
      return this._anisortopy;
    }

    /**
     * 
     * @return 
     */
    public getModel(): CubismModel {
      return this._model;
    }

    /**
     * 
     */
    protected constructor() {
      this._isCulling = false;
      this._isPremultipliedAlpha = false;
      this._anisortopy = 0.0;
      this._model = null;
      this._modelColor = new CubismTextureColor();

      // 
      this._mvpMatrix4x4 = new CubismMatrix44();
      this._mvpMatrix4x4.loadIdentity();
    }

    /**
     * 
     */
    public abstract doDrawModel(): void;

    /**
     * 
     * 
     * @param textureNo 
     * @param indexCount 
     * @param vertexCount 
     * @param indexArray 
     * @param vertexArray 
     * @param uvArray uv
     * @param opacity 
     * @param colorBlendMode 
     * @param invertedMask 
     */
    public abstract drawMesh(
      textureNo: number,
      indexCount: number,
      vertexCount: number,
      indexArray: Uint16Array,
      vertexArray: Float32Array,
      uvArray: Float32Array,
      opacity: number,
      colorBlendMode: CubismBlendMode,
      invertedMask: boolean
    ): void;

    /**
     * 
     */
    public static staticRelease: Function;

    protected _mvpMatrix4x4: CubismMatrix44; // Model-View-Projection 
    protected _modelColor: CubismTextureColor; // RGBA
    protected _isCulling: boolean; // true
    protected _isPremultipliedAlpha: boolean; // true
    protected _anisortopy: any; // 
    protected _model: CubismModel; // 
  }

  export enum CubismBlendMode {
    CubismBlendMode_Normal = 0, // 
    CubismBlendMode_Additive = 1, // 
    CubismBlendMode_Multiplicative = 2 // 
  }

  /**
   * RGBA
   */
  export class CubismTextureColor {
    /**
     * 
     */
    constructor() {
      this.R = 1.0;
      this.G = 1.0;
      this.B = 1.0;
      this.A = 1.0;
    }

    R: number; // 
    G: number; // 
    B: number; // 
    A: number; // 
  }
}
