import { Live2DCubismFramework as cubismjson } from '../utils/cubismjson';
import { Live2DCubismFramework as cubismid } from '../id/cubismid';
import { Live2DCubismFramework as cubismframework } from '../live2dcubismframework';
import { Live2DCubismFramework as csmstring } from '../type/csmstring';
import csmString = csmstring.csmString;
import CubismFramework = cubismframework.CubismFramework;
import CubismIdHandle = cubismid.CubismIdHandle;
import CubismJson = cubismjson.CubismJson;

export namespace Live2DCubismFramework {
  // JSON keys
  const Meta = 'Meta';
  const Duration = 'Duration';
  const Loop = 'Loop';
  const CurveCount = 'CurveCount';
  const Fps = 'Fps';
  const TotalSegmentCount = 'TotalSegmentCount';
  const TotalPointCount = 'TotalPointCount';
  const Curves = 'Curves';
  const Target = 'Target';
  const Id = 'Id';
  const FadeInTime = 'FadeInTime';
  const FadeOutTime = 'FadeOutTime';
  const Segments = 'Segments';
  const UserData = 'UserData';
  const UserDataCount = 'UserDataCount';
  const TotalUserDataSize = 'TotalUserDataSize';
  const Time = 'Time';
  const Value = 'Value';

  /**
   * motion3.json
   */
  export class CubismMotionJson {
    /**
     * 
     * @param buffer motion3.json
     * @param size 
     */
    public constructor(buffer: ArrayBuffer, size: number) {
      this._json = CubismJson.create(buffer, size);
    }

    /**
     * 
     */
    public release(): void {
      CubismJson.delete(this._json);
    }

    /**
     * 
     * @return []
     */
    public getMotionDuration(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(Duration)
        .toFloat();
    }

    /**
     * 
     * @return true 
     * @return false 
     */
    public isMotionLoop(): boolean {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(Loop)
        .toBoolean();
    }

    /**
     * 
     * @return 
     */
    public getMotionCurveCount(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(CurveCount)
        .toInt();
    }

    /**
     * 
     * @return [FPS]
     */
    public getMotionFps(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(Fps)
        .toFloat();
    }

    /**
     * 
     * @return 
     */
    public getMotionTotalSegmentCount(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(TotalSegmentCount)
        .toInt();
    }

    /**
     * 
     * @return 
     */
    public getMotionTotalPointCount(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(TotalPointCount)
        .toInt();
    }

    /**
     * 
     * @return true 
     * @return false 
     */
    public isExistMotionFadeInTime(): boolean {
      return !this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(FadeInTime)
        .isNull();
    }

    /**
     * 
     * @return true 
     * @return false 
     */
    public isExistMotionFadeOutTime(): boolean {
      return !this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(FadeOutTime)
        .isNull();
    }

    /**
     * 
     * @return []
     */
    public getMotionFadeInTime(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(FadeInTime)
        .toFloat();
    }

    /**
     * 
     * @return []
     */
    public getMotionFadeOutTime(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(FadeOutTime)
        .toFloat();
    }

    /**
     * 
     * @param curveIndex 
     * @return 
     */
    public getMotionCurveTarget(curveIndex: number): string {
      return this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(Target)
        .getRawString();
    }

    /**
     * ID
     * @param curveIndex 
     * @return ID
     */
    public getMotionCurveId(curveIndex: number): CubismIdHandle {
      return CubismFramework.getIdManager().getId(
        this._json
          .getRoot()
          .getValueByString(Curves)
          .getValueByIndex(curveIndex)
          .getValueByString(Id)
          .getRawString()
      );
    }

    /**
     * 
     * @param curveIndex 
     * @return true 
     * @return false 
     */
    public isExistMotionCurveFadeInTime(curveIndex: number): boolean {
      return !this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(FadeInTime)
        .isNull();
    }

    /**
     * 
     * @param curveIndex 
     * @return true 
     * @return false 
     */
    public isExistMotionCurveFadeOutTime(curveIndex: number): boolean {
      return !this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(FadeOutTime)
        .isNull();
    }

    /**
     * 
     * @param curveIndex 
     * @return []
     */
    public getMotionCurveFadeInTime(curveIndex: number): number {
      return this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(FadeInTime)
        .toFloat();
    }

    /**
     * 
     * @param curveIndex 
     * @return []
     */
    public getMotionCurveFadeOutTime(curveIndex: number): number {
      return this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(FadeOutTime)
        .toFloat();
    }

    /**
     * 
     * @param curveIndex 
     * @return 
     */
    public getMotionCurveSegmentCount(curveIndex: number): number {
      return this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(Segments)
        .getVector()
        .getSize();
    }

    /**
     * 
     * @param curveIndex 
     * @param segmentIndex 
     * @return 
     */
    public getMotionCurveSegment(
      curveIndex: number,
      segmentIndex: number
    ): number {
      return this._json
        .getRoot()
        .getValueByString(Curves)
        .getValueByIndex(curveIndex)
        .getValueByString(Segments)
        .getValueByIndex(segmentIndex)
        .toFloat();
    }

    /**
     * 
     * @return 
     */
    public getEventCount(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(UserDataCount)
        .toInt();
    }

    /**
     *  
     * @return 
     */
    public getTotalEventValueSize(): number {
      return this._json
        .getRoot()
        .getValueByString(Meta)
        .getValueByString(TotalUserDataSize)
        .toInt();
    }

    /**
     * 
     * @param userDataIndex 
     * @return []
     */
    public getEventTime(userDataIndex: number): number {
      return this._json
        .getRoot()
        .getValueByString(UserData)
        .getValueByIndex(userDataIndex)
        .getValueByString(Time)
        .toInt();
    }

    /**
     * 
     * @param userDataIndex 
     * @return 
     */
    public getEventValue(userDataIndex: number): csmString {
      return new csmString(
        this._json
          .getRoot()
          .getValueByString(UserData)
          .getValueByIndex(userDataIndex)
          .getValueByString(Value)
          .getRawString()
      );
    }

    _json: CubismJson; // motion3.json
  }
}
